@font-face {
  font-family: "OsWald-light";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  font-style: normal;
}
body {
  margin: auto;
  font-family: "OsWald-light";
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat fixed center;
  background-size: cover;
  background-color: rgb(73, 72, 72);
  background-blend-mode: multiply;
  color: whitesmoke;
  min-height: 100svh;
  position: relative;
}

button {
  appearance: none;
  background-color: #fcbf49;
  border-width: 0;
  box-sizing: border-box;
  color: #000000;
  cursor: pointer;
  display: inline-block;
  font-family: "Oswald-bold";
  font-size: 1rem;
  line-height: 1rem;
  margin: 0;
  opacity: 1;
  outline: 0;
  padding: 1rem;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-rendering: geometricprecision;
  text-transform: uppercase;
  transition: opacity 300ms cubic-bezier(0.694, 0, 0.335, 1), background-color 100ms cubic-bezier(0.694, 0, 0.335, 1), color 100ms cubic-bezier(0.694, 0, 0.335, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

button:before {
  animation: opacityFallbackOut 0.5s step-end forwards;
  backface-visibility: hidden;
  background-color: #d62828;
  clip-path: polygon(-1% 0, 0 0, -25% 100%, -1% 100%);
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transform: translateZ(0);
  transition: clip-path 0.5s cubic-bezier(0.165, 0.84, 0.44, 1), -webkit-clip-path 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  width: 100%;
}

button:hover:before {
  animation: opacityFallbackIn 0s step-start forwards;
  clip-path: polygon(0 0, 101% 0, 101% 101%, 0 101%);
}

button span {
  z-index: 1;
  position: relative;
}

#header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: start;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px 40px;
}
#header h1 {
  font-size: 1.9rem;
  font-family: "Oswald-Bold";
}
#header .active {
  background-color: #d62828;
}
#header nav {
  font-size: 1.9rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
}
#header button {
  background-color: transparent;
  color: whitesmoke;
  font-size: 1.2rem;
  padding: 0.6rem;
}

#content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90svh;
  height: 90vh;
  padding: 20px 40px;
}

#homePage {
  display: grid;
  gap: 10px;
  width: 100%;
  align-content: center;
  justify-content: center;
}
#homePage h1 {
  font-size: 3rem;
}
#homePage h2 {
  font-size: 1.7rem;
}
#homePage .homePage-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 20px;
}

#contactUsPage {
  display: grid;
  width: 100%;
  height: 100%;
  gap: 1rem;
  grid-template-columns: 1fr 0.8fr;
  padding: 1.5rem 2rem;
}
#contactUsPage h1 {
  font-size: 2rem;
  font-family: "OsWald-light";
}
#contactUsPage div {
  display: grid;
}
#contactUsPage img {
  display: block;
  width: 100%;
  aspect-ratio: 1/1;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  border: 2px solid black;
}
#contactUsPage .contacts {
  display: grid;
  align-items: center;
  grid-template-rows: 0.5fr 1fr;
}
#contactUsPage .contacts .contact-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  text-align: center;
}
#contactUsPage .contacts .contact-container .address-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
#contactUsPage .contacts .contact-container .address-container div {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
#contactUsPage .contacts .socials {
  display: flex;
  flex-direction: row;
  gap: 60px;
}
#contactUsPage .contacts .socials a {
  color: whitesmoke;
}
#contactUsPage .form-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
#contactUsPage .form-container form {
  padding: 2rem 1.5rem;
  display: grid;
  gap: 20px;
  width: 800px;
  align-self: center;
  background-color: rgba(0, 0, 0, 0.4);
}
#contactUsPage .form-container form div {
  display: grid;
  gap: 16px;
}
#contactUsPage .form-container form textarea {
  resize: none;
}
#contactUsPage .form-container form button {
  justify-self: center;
  width: 50%;
  max-width: 360px;
}

#menuPage {
  display: grid;
  width: 100%;
  height: 100%;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  padding: 40px;
  justify-items: center;
}
#menuPage h1 {
  font-size: 2rem;
}
#menuPage .menuBox {
  display: grid;
  grid-template-rows: 0.1fr 1fr;
  font-size: 1.2rem;
}
#menuPage .menuBox ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  padding: 40px 20px 10px 20px;
